import VueRouter from 'vue-router'
import index from '../page/index.vue'
import chahua from '../page/chahua.vue'
import about from '../page/about.vue'
import schoolNew from '../page/schoolNew.vue'
const router = new VueRouter({
    mode: 'hash',
    routes: [
        {
            path: '/',
            component: index,
            meta:{title:'艺术设计学院'}
        },
        {
            path: '/chahua',
            component: chahua,
            meta:{title:'艺术学院插画'}

        },
        {
            path: '/about',
            component: about,
            meta:{title:'关于'}

        },
        {
            path: '/yaowen',
            component: schoolNew,
            meta:{title:'学校要闻'}

        },
    ]
})
// 全局定义后置路由守卫 切换路由后调用
router.afterEach((to,from)=>{
    document.title=to.meta.title || '艺术设计学院'
  
 })

export default router